import { expect } from 'chai';

import { render } from '@testing-library/preact';

import { Divider } from '../Divider';

describe('Test Divider component', () => {
  it('divider render', async function () {
    const content = render(<Divider />);
    const divider = await content.findByRole('separator');
    const classList = divider.classList;
    expect(divider).not.null;
    expect(classList.length).equals(1);
  });
});
